import { Navigate } from "react-router-dom";
import Favorite from "./layouts/favorite";
import MainPage from "./layouts/mainPage";
import StudentPage from "./pages/studentPage";

export const routes = [
  {
    path: "",
    breadcrumb: "",
    element: <MainPage />,
    display: true,
    title: "",
    children: [{
      path: ":id",
      element: <StudentPage />,
      display: false,
      title: " ",
    }]
  },
  {
    path: "/favorite",
    element: <Favorite />,
    display: true,
    title: "",
  },
  {
    path: "*",
    element: <Navigate to="/" replace={true} />,
  },

];