export const transformAge = (age) => {
   if (age < 0) {
      return (
         "    "
      );
   }
   let word;
   if (age >= 5 && age <= 20) {
      word = age + " "
   } else {
      const rule = ["2", "3", "4"];
      const ageArr = age.toString().split("").reverse();
      if (ageArr[0] === "1") return age + " "
      if (rule.includes(ageArr[0])) {
         word = age + " "
      } else {
         word = age + " "
      }
   }
   return (
      word
   );
};